.time-circle {
    width: 100%;
    max-width: 20rem;
    position: relative;
    display: grid;
}
.time-circle::before {
    border-radius: 100%;
    animation: 1s linear infinite alternate abberation, 4s linear infinite spin;
    padding-top: 100%;
    content: "";
    position: relative;
    pointer-events: none;
    z-index: 1;
}

