

@keyframes abberation {

    from {
        box-shadow: 0 0 0 0  hotpink, 0 0 0 0 darkviolet;
    }
    to {

        box-shadow: 0.25rem 0.125rem 0 0 hotpink, -0.25rem -0.125rem 0 0 darkviolet;
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
.stopwatch {
    /*margin-top: 70px;*/
    padding-top: 180px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    height: 100vh;
    align-items: center;
    justify-content: center;
}

.buttons {
    margin-top: 4rem;
    display: flex;
    gap: 1.5rem;
    justify-items: center;
}
.play-pause-reset {
    width: 4.5rem;
    height: 4.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    transition: background 0.3s;
}
.icons{
    font-size: 2.5rem;
}
.btnsBackground{
    margin-top: 4rem;
    display: flex;
    justify-items: flex-end;
    gap: .5rem;
    width: 2.5rem;
    height: 2.5rem;
    cursor: pointer;
}
.brightBackground{
    color: darkseagreen;
}
.darkBackground{
    color: #6C7CB3;
}
.dark{
    color: #fafafa;
    background: #282937;
}
.btnDark{
    background: #6C7CB3;
}
.btnDark:hover {
    box-shadow: 0 0 15px hotpink;
}
.bright{
    color: darkslateblue;
    background: beige;
}
.btnBright{
    background: darkseagreen;
}
.btnBright:hover {
    box-shadow: 0 0 15px orange;
}