.modal{
    height: 67vh;
    width: 45vh;
    position: fixed;
    z-index: 1;
    border-radius: 20px;
    background: white;
    color: #282937;
}
.hidden{
    display: none;
}
.modalSetting{
    display: grid;
    justify-content: center;
    text-align: center;
}
.setting-btn{
    text-align: center;
    margin-top: 40px;
}
.bnt-modal{
    background: #282937;
    color: #fafafa;
    height: 2.5rem;
    width: 11rem;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 2.5rem;
}
hr{
    width: 250px;
}
h2, h3{
    margin-bottom: 5px;
    padding-left: 10px;
}
.setting-time{
    padding: 10px;
    display: grid;
    justify-items: center;
    font-size: 20px;
}
.setting-label{
    color: grey;
    padding: 20px;
}
.setting-input{
    width: 4rem;
    height: 2.5rem;
    font-size: 18px;
    color: #3C4652;
    border-radius: 5px;
    border: none;
    background: rgba(0, 0, 0, .1);
    text-align: center;
}