.tagsContainer{
    padding: 10px 15px;
    margin: 0 auto;
}
.tag{
    font-size: 1rem;
    padding: 10px 15px;
    background: #89AC76;
    color: #fafafa;
    margin: 0 10px 15px;
    border: none;
    border-radius: 30px;
    cursor: pointer;
}
.tag:hover{
    box-shadow: 0 0 7px white;
}
.active{
    background: #98cc7c;
    box-shadow: 0 0 7px white;
}