
@keyframes aberration {
    from {
        box-shadow: 0 0 0 0  #89AC76, 0 0 0 0 #edbe68;
    }
    to {

        box-shadow: 0.25rem 0.125rem 0 0 #89AC76, -0.25rem -0.125rem 0 0 #edbe68;
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
.time-circle-pomodoro {
    width: 100%;
    max-width: 20rem;
    position: relative;
    display: grid;
}
.time-circle-pomodoro::before {
    border-radius: 100%;
    animation: 1s linear infinite alternate aberration, 4s linear infinite spin;
    padding-top: 100%;
    content: "";
    position: relative;
    pointer-events: none;
    z-index: 1;
}
.pomodoro{
    background: #8a63a9;
    /*background: #d0e584;*/
    color: #fafafa;
    height: 100vh;
}
.time {
    padding-top: 1rem;
    font-size: 4.3rem;
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.timer{
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}
.bnt-circle {
    width: 4.5rem;
    height: 4.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    transition: background 0.3s;
    background: #89AC76;
    /*background: #edbe68;*/
}
.icons{
    font-size: 2.5rem;
}
.bnt-circle:hover, .bntBreak button:hover {
    box-shadow: 0 0 10px white;
}
.setting{
    display: flex;
    justify-content: flex-end;
    padding: 6rem 2rem 0 0;
    cursor: pointer;
    font-size: 2rem;
}
.btnSetting:hover{
    animation: spin 5s linear 0s infinite;
}
