body {
  margin: 0;
  font-family: 'Helvetica', 'Helvetica Nueue', sans-serif;
  text-decoration: none;
}
.app {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
}
header{
  width: 100%;
  display: flex;
  justify-content: space-around;
  top: 0;
  padding: 1.5rem 0;
  font-size: 1.3rem;
  text-decoration: none;
  background: rgba(225, 222, 214, .5);

  text-align: center;
  position: fixed;
}

